@font-face {
  font-family: 'Proxima Nova - Extrabld';
  src: url('fonts/ProximaNova-Extrabld.eot');
  src: url('fonts/ProximaNova-Extrabld.eot?#iefix') format('embedded-opentype'),
  url('fonts/ProximaNova-Extrabld.woff') format('woff'),
  url('fonts/ProximaNova-Extrabld.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url('fonts/ProximaNova-Regular.eot');
  src: url('fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
  url('fonts/ProximaNova-Regular.woff') format('woff'),
  url('fonts/ProximaNova-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

html, body {
  font-family: 'Proxima Nova', sans-serif;
  margin: 0;
  padding: 0;
}

body{
  color: #fff;
  display: flex;
  background-color: #33b7f7;
  background-image: linear-gradient(to top, rgba(51, 181, 245, 0) 0%, #662584 100%),  url('../images/back.png');
  background-position: top left, bottom left;
  background-repeat: no-repeat, no-repeat;
  height: 100vh;
}

.logo {
  width: 161px;
  height: 28px;
  margin: 0px;
  display: block;
  background: url('data:image/png;base64,');
}
.section {
  width: 1024px;
  margin: 50px auto;
  .left, .right {
    width: 50%;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .left{
    float: left;
    img {
      width: 340px;
    }
  }
  .right{
    float: right;
  }
}
p{
  font-size: 24px;
  font-weight: 400;
  line-height: 33px;
  width: 340px;
  margin-top: 2px;
}
h1 {
  font-family: "Proxima Nova - Extrabld";
  font-size: 60px;
  font-weight: 400;
  line-height: 0;
}

form{
  margin-top: 80px;
  label{
    width: 303px;
    display: block;
    font-size: 12px;
    letter-spacing: 0.36px;
  }
  button, input {
    background: transparent;
    color: #fff;

    outline: none;
    border: 0;
  }
  input.email {
    width: 303px;
    border-bottom: 1px solid #3ab3f3;
    letter-spacing: 0.54px;
    font-size: 18px;
    padding: 10px 0 7px 0;
    color: #fff;
  }
  .button {
    cursor: pointer;
    margin-top: 35px;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.4px;
    padding: 19px 62px;
    border: 1px solid #fff;
    border-radius: 30px;
    &:hover{
      background: #fff;
      color: #3ab3f3;
    }
  }

}